@-webkit-keyframes Neon {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
    text-shadow: -1px 5px #F148FB;
    box-shadow: 2px 2px 10px #560A86;
  }
}

@keyframes Neon {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.1);
    text-shadow: -1px 5px #F148FB;
    box-shadow: 2px 2px 10px #560A86;
  }
}

